import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
//import { InAppBrowser, InAppBrowserOptions } from '@ionic-native/in-app-browser';
import { ThemeableBrowser, ThemeableBrowserOptions, ThemeableBrowserObject} from '@ionic-native/themeable-browser';

/**
 * Generated class for the ClaimsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-claims',
  templateUrl: 'claims.html',
})
export class ClaimsPage {


 

  constructor(public navCtrl: NavController, public navParams: NavParams, private themeableBrowser: ThemeableBrowser) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ClaimsPage');
  }

  openOuterPage(browserUrl){
    
 //   this.iab.create(browserUrl, "_self", this.options);
 const options: ThemeableBrowserOptions = {
    toolbar: {
        height: 44,
        color: '#f0f0f0ff'
    },
    title: {
        color: '#003264ff',
        showPageTitle: true,
        staticText: 'Smart Genie'
    },
    closeButton: {
      wwwImage: 'assets/img/cancel.png',
      align: 'left',
      event: 'closePressed'
    },
  };
 const browser: ThemeableBrowserObject =this.themeableBrowser.create(browserUrl, '_blank', options);
 browser.on('closePressed').subscribe(() => {
    browser.close();
  })
 //browser.show();
  }

}



